<script>
// import BarExample from './charts/Bar'
import { GChart } from 'vue-google-charts'
const axios = require('axios');
var _ = require('lodash');
// var moment = require('moment');
export default {
  components: {
      // BarExample
      GChart
    },
    data () {
      return {
        payload: {
          opcion: 'contratos',
        },
        data: [],
        // Array will be automatically processed with visualization.arrayToDataTable function
        chartData: [
          ['Años'],
          ['Enero'],
          ['Febrero'],
          ['Marzo'],
          ['Abril'],
          ['Mayo'],
          ['Junio'],
          ['Julio'],
          ['Agosto'],
          ['Septiembre'],
          ['Octubre'],
          ['Noviembre'],
          ['Diciembre'],
        ],
        chartOptions: {
          chart: {
            title: 'Company Performance',
            subtitle: 'Sales, Expenses, and Profit: 2014-2017',
          }
        }
      }
    },
    mounted () {
      let personForm = this.toFormData(this.payload);
      axios
      .post('./mysqli.php', personForm)
      .then(response => {
        this.info = response.data.bpi
        _.forEach(response.data, (value, key) => {
          this.data.push(value.total);
          this.chartData[0].push(key)
          var  array = this.recorreArray(Object.values(value))
        });
      })
      .catch(error => {
          console.error(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
    },
    methods: {
      recorreArray: function (array) {
        let x1 = _.find(array, (o) => { return parseInt(o.mes) === 1; })
        let x2 = _.find(array, (o) => { return parseInt(o.mes) === 2; })
        let x3 = _.find(array, (o) => { return parseInt(o.mes) === 3; })
        let x4 = _.find(array, (o) => { return parseInt(o.mes) === 4; })
        let x5 = _.find(array, (o) => { return parseInt(o.mes) === 5; })
        let x6 = _.find(array, (o) => { return parseInt(o.mes) === 6; })
        let x7 = _.find(array, (o) => { return parseInt(o.mes) === 7; })
        let x8 = _.find(array, (o) => { return parseInt(o.mes) === 8; })
        let x9 = _.find(array, (o) => { return parseInt(o.mes) === 9; })
        let x10 = _.find(array, (o) => { return parseInt(o.mes) === 10; })
        let x11 = _.find(array, (o) => { return parseInt(o.mes) === 11; })
        let x12 = _.find(array, (o) => { return parseInt(o.mes) === 12; })
        
        if (_.isUndefined(x1)) {
          this.chartData[1].push(0)
        } else {
          this.chartData[1].push(parseInt(x1.total))
        }
        if (_.isUndefined(x2)) {
          this.chartData[2].push(0)
        } else {
          this.chartData[2].push(parseInt(x2.total))
        }
        if (_.isUndefined(x3)) {
          this.chartData[3].push(0)
        } else {
          this.chartData[3].push(parseInt(x3.total))
        }
        if (_.isUndefined(x4)) {
          this.chartData[4].push(0)
        } else {
          this.chartData[4].push(parseInt(x4.total))
        }
        if (_.isUndefined(x5)) {
          this.chartData[5].push(0)
        } else {
          this.chartData[5].push(parseInt(x5.total))
        }
        if (_.isUndefined(x6)) {
          this.chartData[6].push(0)
        } else {
          this.chartData[6].push(parseInt(x6.total))
        }
        if (_.isUndefined(x7)) {
          this.chartData[7].push(0)
        } else {
          this.chartData[7].push(parseInt(x7.total))
        }
        if (_.isUndefined(x8)) {
          this.chartData[8].push(0)
        } else {
          this.chartData[8].push(parseInt(x8.total))
        }
        if (_.isUndefined(x9)) {
          this.chartData[9].push(0)
        } else {
          this.chartData[9].push(parseInt(x10.total))
        }
        if (_.isUndefined(x10)) {
          this.chartData[10].push(0)
        } else {
          this.chartData[10].push(parseInt(x10.total))
        }
        if (_.isUndefined(x11)) {
          this.chartData[11].push(0)
        } else {
          this.chartData[11].push(parseInt(x11.total))
        }
        if (_.isUndefined(x12)) {
          this.chartData[12].push(0)
        } else {
          this.chartData[12].push(parseInt(x12.total))
        }
      },
      sleep: function (ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
      },
      toFormData: function(obj) {
        let formData = new FormData();
        for(let key in obj) {
          formData.append(key, obj[key]);
          }
          return formData;
        }
    },
    name: 'HelloWorld',
    props: {
      msg: String
    },
    
}
</script>

